import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import { makeStyles } from '@material-ui/core/styles'
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";

const dispatchedEvents = [];

const SrsMuiTabs = function (props, ref) {
  let defaultValue = props.tabitems?.[0]?.value || "";
  const [value, setValue] = React.useState(defaultValue);
  const onChange = (evt, newValue) => {
    setValue((prevVal) => newValue);
  }

  const useStyles = makeStyles((theme) => {
    return props.classes || {};
  });
  const classes = useStyles(props.muiTheme);

  return (<div>
      <Tabs
        className={classes[props.tabsclass] || ""}
        value={value}
        onChange={onChange}
        indicatorColor={props.indicatorColor}
        textColor={props.textColor}
        centered={props.centered}
        variant={props.variant}
        sx={props.sx}
        theme={props.muiTheme}
      >
        {
          props.tabitems.map((tabItem) => {
            return (<Tab {...tabItem} className={classes[props.tabclass] || ""} theme={props.muiTheme}/>);
          })
        }
      </Tabs>
      {
        props.tabitems.map((tabItem, indx) => {
          return value == tabItem.value? <span id={tabItem.id || indx}><slot name={tabItem.slotname}/></span>: null
        })
      }
    </div>
  );
};

const styles = `
  /* Custom styles for the MuiTabs component */
`;

const props = [
  {
    name: "tabitems",
    defaultValue: [],
    type: "object"
  },
  {
    name: "value",
    defaultValue: "",
    type: "string",
  },
  {
    name: "classes",
    defaultValue: {},
    type: "object",
  },
  {
    name: "tabsclass",
    defaultValue: "",
    type: "string",
  },
  {
    name: "tabclass",
    defaultValue: "",
    type: "string",
  },
  {
    name: "onChange",
    defaultValue: null,
    type: "function",
  },
  {
    name: "indicatorColor",
    defaultValue: "primary",
    type: "string",
  },
  {
    name: "textColor",
    defaultValue: "inherit",
    type: "string",
  },
  {
    name: "centered",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "variant",
    defaultValue: "standard",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  },
  {
    name: "classes",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Tabs if needed
];

const slots = [
  {
    name: "default",
    allowed: ["srs-tab"],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiTabs),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
